.b-home-img {
    background-image: url(/img/intercambio_editado.jpg);
    /*background-image: url(https://via.placeholder.com/1100x300);*/
    background-size: contain;
    background-repeat: no-repeat;
    height: 400px;
    padding-top: 74px;
}

.side-text {
    background: rgba(0, 0, 0, 0.8);
    border-radius: 25px;
    position: absolute;
    top: 40%;
    max-width:700px;
    left: 35%; 
    transform: translate(-50%, -50%);
}

.rounded-bg {
    background: #f19800;
    height: 25em;
    border-color: #f19800;
    border-style: solid;
    width: inherit;
    position: absolute;
    top: 76px;
    border-width: medium;
    z-index: -15;
}

/* .background-presentacion{
    background: rgba(0, 0, 0, 0.7);
    border-radius: 25px;
} */

@media (max-width: 1100px) {
    .side-text {
        position: relative;
        top: 0;
        left: 0;
        transform: translate(0, 0);
        max-width: 1100px;
    }
    .rounded-bg{
        border-radius: 0 0 300px 300px;
    }
    .carousel-caption h1 {
        font-size: 2.8rem !important;
    }
    @media screen and (max-width: 1100px){   
        #main-img{      
            content:url("/img/intercambio_mediano.jpg");
        } 
    }
}

.carousel-caption {
    bottom: 30% !important;
}

.carousel-caption h1 {
    font-size: 3.8rem;
    font-weight: 700;
    letter-spacing: .3rem;
    text-shadow: .1rem .1rem .8rem black;
}

@media (max-width: 862px) {
    .carousel-caption h1{
        font-size: 1.8rem !important;
    }
}